// src/router/index.js
import { createRouter, createWebHashHistory } from 'vue-router';
// import HomePage from '../views/HomePage.vue'; // 导入你的页面组件
import VerificationResult from '../views/VerificationResult.vue'; // 导入另一个页面组件
import Permission from '../views/Permission.vue'; // 导入权限说明页面

const routes = [
  {
    path: '/',
    name: 'Home',
    component: VerificationResult,
  },
  {
    path: '/result',
    name: 'VerificationResult',
    component: VerificationResult,
  },
  {
    path: '/permission',
    name: 'Permission',
    component: Permission,
    meta: {
      title: 'Resolving Camera Issues',
    },
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

// 设置页面标题
router.beforeEach((to) => {
  if (to.meta.title) {
    document.title = to.meta.title;
  }
});

export default router;
