<template>
  <div class="page-container">
    <header class="header">
      <h1 class="title">Resolving Camera Issues</h1>
      <div class="menu-wrapper" ref="menu">
        <button class="menu-toggle" @click="toggleMenu">
          {{ currentLabel }} <span class="arrow">▾</span>
        </button>
        <transition name="fade">
          <div v-show="open" class="dropdown">
            <div
              v-for="item in menuItems"
              :key="item.key"
              class="dropdown-item"
              :class="{ selected: currentSection === item.key }"
              @click="scrollToSection(item.key)"
            >
              {{ item.label }}
            </div>
          </div>
        </transition>
      </div>
    </header>

    <div class="permission-page">
      <div class="wrapper">
        <div class="layout">
          <div class="content-container">
            <div class="recommend">
              We recommend clicking the dropdown in the top-right corner to select your browser version and quickly navigate to the relevant section.
              You can also scroll down the page to find your browser version and follow the instructions to enable camera access.
            </div>
            <div
              v-for="item in menuItems"
              :key="item.key"
              :ref="el => sectionRefs[item.key] = el"
              class="content-block"
            >
              <h3>{{ item.label }}</h3>
              <p>{{ item.steps[0] }}</p>
              <img :src="item.images[0]" :alt="item.label">
              <p>{{ item.steps[1] }}</p>
              <img :src="item.images[1]" :alt="item.label">
              <p v-if="item.steps[2]">{{ item.steps[2] }}</p>
              <img v-if="item.steps[2]" :src="item.images[2]" :alt="item.label">
              <p v-if="item.steps[3]">{{ item.steps[3] }}</p>
              <img v-if="item.steps[3]" :src="item.images[3]" :alt="item.label">
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, reactive, onMounted, onBeforeUnmount, nextTick, computed } from 'vue'

export default {
  name: 'CameraPermission',
  setup() {
    const open = ref(false)
    const menu = ref(null)
    const currentSection = ref('chromeDesktop')
    const sectionRefs = reactive({})

    const toggleMenu = () => (open.value = !open.value)

    const handleClickOutside = (e) => {
      if (menu.value && !menu.value.contains(e.target)) {
        open.value = false
      }
    }

    const scrollToSection = async (key) => {
      open.value = false
      currentSection.value = key
      await nextTick()

      const el = sectionRefs[key]
      if (el) {
        const offset = 80
        const top = el.getBoundingClientRect().top + window.scrollY - offset
        window.scrollTo({ top, behavior: 'smooth' })
      } else {
        console.warn('Section not found:', key)
      }
    }


    const currentLabel = computed(() => {
      const item = menuItems.find(i => i.key === currentSection.value)
      return item ? item.label : 'Browsers'
    })

    const menuItems = [
      {
        key: 'chromeDesktop',
        label: 'Chrome (macos/windows)',
        steps: [
          '1. Click the icon in the browser\'s address bar',
          '2. Select the camera module in the pop-up menu',
          '3. Toggle the switch on the right side'
        ],
        images: [
          new URL('@/assets/images/permission/pc_chrome1.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/pc_chrome2.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/pc_chrome3.jpeg', import.meta.url).href
        ]
      },
      {
        key: 'chromeMoblile',
        label: 'Chrome (Android/iOS)',
        steps: [
          '1. When the page loads, a permission request popup will appear — click "Allow".',
          '2. If you selected "Allow" in the first step, you can click the icon on the left side of the browser’s address bar.',
          '3. In the dropdown menu, select "Permissions".',
          '4. Find the camera option and toggle the switch to enable it.'
        ],
        images: [
          new URL('@/assets/images/permission/chrome_mobile1.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/chrome_mobile2.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/chrome_mobile3.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/chrome_mobile4.jpeg', import.meta.url).href,
        ]
      },
      {
        key: 'safariMac',
        label: 'Safari (macOS)',
        steps: [
          '1. Click the "Safari" menu and select "Preferences"',
          '2. In the "Websites" tab, select "Camera"',
          '3. Ensure camera access is allowed'
        ],
        images: [
          new URL('@/assets/images/permission/pc_safari1.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/pc_safari2.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/pc_safari2.jpeg', import.meta.url).href
        ]
      },
      {
        key: 'safariIOS',
        label: 'Safari (iOS)',
        steps: [
          '1. Tap the icon on the left side of Safari',
          '2. Tap "Website Settings"',
          '3. Tap "Camera" and select "Allow"'
        ],
        images: [
          new URL('@/assets/images/permission/ios_safari1.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/ios_safari2.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/ios_safari3.jpeg', import.meta.url).href
        ]
      },
      {
        key: 'systemIOS',
        label: 'System Permissions (iOS)',
        steps: [
          '1. Go to Settings',
          '2. Tap Privacy and select Camera',
          '3. Enable camera access for your browser'
        ],
        images: [
          new URL('@/assets/images/permission/ios_system.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/ios_system1.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/ios_system2.jpeg', import.meta.url).href
        ]
      },
      {
        key: 'systemAndroid',
        label: 'System Permissions (android)',
        steps: [
          '1. Go to Settings',
          '2. Tap Privacy and select Camera,  Enable camera access for your browser',
        ],
        images: [
          new URL('@/assets/images/permission/android1.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/android2.jpeg', import.meta.url).href,
        ]
      },
      {
        key: 'systemWindows',
        label: 'System Permissions (windows)',
        steps: [
          '1. Go to Settings and click Privacy.',
          '2. Navigate to Camera.',
          '3. Set Camera permission to Allow'
        ],
        images: [
          new URL('@/assets/images/permission/windows1.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/windows2.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/windows3.jpeg', import.meta.url).href
        ]
      },
      {
        key: 'systemMacos',
        label: 'System Permissions (macos)',
        steps: [
          '1. Go to Settings and click Privacy.',
          '2. Navigate to Camera.',
          '3. Set Camera permission to Allow'
        ],
        images: [
          new URL('@/assets/images/permission/macos1.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/macos2.jpeg', import.meta.url).href,
          new URL('@/assets/images/permission/macos3.jpeg', import.meta.url).href
        ]
      }
    ]

    onMounted(() => {
      const hash = window.location.hash.replace('#', '')
      if (hash) scrollToSection(hash)

      const observer = new IntersectionObserver((entries) => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            const matched = Object.entries(sectionRefs).find(([key, el]) => el === entry.target)
            if (matched) currentSection.value = matched[0]
          }
        })
      }, { threshold: 0.5 })

      nextTick(() => {
        setTimeout(() => {
          Object.values(sectionRefs).forEach(el => observer.observe(el))
        }, 100)
      })

      document.addEventListener('click', handleClickOutside)
    })

    onBeforeUnmount(() => {
      document.removeEventListener('click', handleClickOutside)
    })

    return {
      open,
      menu,
      toggleMenu,
      currentSection,
      scrollToSection,
      sectionRefs,
      menuItems,
      currentLabel,
    }
  },
}
</script>

