
<script>
import { ref, onMounted } from "vue";
import axios from "axios";
import { useI18n } from 'vue-i18n';
import { useRoute } from "vue-router"; // for accessing URL params

export default {
  setup() {
    // Reactive state variables
    const { t } = useI18n();
    const faceDetail = ref({
      faceResult: "",
      faceImageFar: "",
      faceImageNear: "",
      auditImageUrl: "",
      faceLivenessScore: 0,
      faceSimilarityScore: 0,
    });
    const docDetail =  ref({
      docType: {
        front: "",
        back: ""
      },
      subDocType: {
        front: "",
        back: ""
      },
      qualityLabels: {
        front: [],
        back: []
      },
      ocrInfo : {
      front: {
        gender: "",
        name: "",
        address: "",
        idNumber: "",
        religion: "",
        birthday: "",
        citizen: ""
      },
      back: {
        idNumber: ""
      }
    },});
    const haserror = ref(false);
    const signatureId = ref("");

    const responseData = ref("");

    const docDatabaseDetail = ref({
      // "bvn": "22********4",
      // "firstName": "T****E",
      // "middleName": "C****T",
      // "lastName": "I***A",
      // "gender": "FEMALE",
      // "birthday": "1994/07/13",
      // "phoneNumber": "081******05",
      // "phoneNumber2": ""
    });

    const idvResult = ref("");
    const errorCode = ref("");
    const faceResult = ref("");
    const faceImageFar = ref("");
    const faceImageNear = ref("");
    const auditImageUrl = ref("");
    const fullUrl = ref(window.location.href);
    const docType = ref({
      front: "",
      back: ""
    });

    const ocrInfo = ref({
      front: {
        gender: "",
        name: "",
        address: "",
        idNumber: "",
        religion: "",
        birthday: "",
        citizen: ""
      },
      back: {
        idNumber: ""
      }
    });
    const loading = ref(true); // Loading state

    // Get the token from the URL using vue-router's useRoute hook
    const route = useRoute();
    const token = route.query?.token || "";
    const sid = route.query?.signatureId || "";
    signatureId.value = sid;
    const country = route.query?.country;
 

    // stg环境
    // const countrys ={
    //   "PHILIPPINES": "k8s-ph-api.advai.net",
    //   "MALAYSIA": "k8s-my-api.advai.net",
    //   "THAILAND": "k8s-th-api.advai.net",
    //   "INDONESIA": "k8s-api.advai.net"
    // }
    // 线上
    const countrys ={
      "INDONESIA": "api.advance.ai",
      "SINGAPORE": "sg-api.advance.ai",
      "PHILIPPINES": "ph-api.advance.ai",
      "MALAYSIA": "my-api.advance.ai",
      "THAILAND": "th-api.advance.ai"
    }
  
    // countrys(country)
    // alert(sid)
    if(!countrys[country]){
      // alert('country match failed')
    }

    // Fetch data on component mount
    const fetchData = async () => {
      try {
        // const response = await axios.post('https://'+countrys[country]+'/intl/openapi/identity-risk/idvs-h5/ekyc/v1/get-result', {
          // const response = await axios.post('/h5-idv-demo/api/product/v1/getTtsResult', {
          const response = await axios.post('https://k8s-h5.advai.net/h5-idv-demo/api/product/v1/getTtsResult', {
          // signatureId: sid,
          "country": country,
          "signatureId": sid,
          "token": token,    
        }, {
          headers: {
            'Content-Type': 'application/json',
            // 'X-ACCESS-TOKEN': token,
          },
        });
        
        // Handle API response data
        const data = response.data.data;
        responseData.value = response.data.data.docDetail;
        //  alert(JSON.stringify(data));
        signatureId.value = data.signatureId;
        idvResult.value = data.idvResult;
        errorCode.value = data.errorCode;
        docDetail.value = data.docDetail;
        faceDetail.value = data.faceDetail;
        docDatabaseDetail.value = data.docDatabaseDetail
        // faceResult.value = data.faceDetail.faceResult;
        // Set loading to false once data is loaded
        loading.value = false;
      } catch (error) {
        console.error('Error fetching data:', error);
        loading.value = false; // In case of error, stop loading
        haserror.value = true;
      }
    };

    // Run fetchData when the component is mounted
    onMounted(() => {
      if (token) {
        fetchData();
      } else {
        console.error("Token not found in the URL");
        loading.value = false;
      }
    });

    // Submit handler
    const submitVerification = () => {
     
      window.location.reload();
      fetchData();
      console.log("Verification submitted");
    };

    return {
      docDetail,
      responseData,
      signatureId,
      idvResult,
      errorCode,
      faceResult,
      faceImageFar,
      faceImageNear,
      auditImageUrl,
      docType,
      ocrInfo,
      submitVerification,
      fullUrl,
      loading,
      faceDetail,
      docDatabaseDetail,
      t
    };
  },
};
</script>

