    </div>
    </div>
      <div class="subtitleBox">
        <div> {{ t('idResult') }}</div>
        <div>{{ docDetail?.ocrResult || 'NULL' }}</div>
      </div>
      <div class="image-section" v-if="docDetail">
        <div v-if="docDetail?.docFrontImage" class="image-box">
          <img :src="docDetail?.docFrontImage" alt="front image" />
