        </div>
        <div v-if="docDetail?.docBackImage" class="image-box">
          <img :src="docDetail?.docBackImage" alt="back image" />
          <div class="overlay">{{ t('back') }}</div>
        </div>
      </div>

      <div class="result-details-card" v-if="docDetail?.ocrInfo?.front?.idNumber">
        <div class="cardTitle">Optical Character Recognition (OCR)</div>
        <div class="resluts">
          <div>{{ t('idNumber') }}</div>
          <div>{{ docDetail?.ocrInfo?.front?.idNumber }}</div>
